.card {
  width: 272px;
  min-height: 208px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 4px;
  position: relative;
  cursor: pointer;
}

.price {
  display: flex;
  gap: 8px;
}

.image {
  width: 240px;
}

.link {
  text-decoration: none;
  color: inherit;
}
